import { LoadingButton } from "@mui/lab";
// material
import {
  Box,
  Card,
  Container,
  FormHelperText,
  Grid,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { Form, FormikProvider, useFormik } from "formik";
import { useSnackbar } from "notistack";
import { useCallback } from "react";
import HeaderBreadcrumbs from "../../components/general/HeaderBreadcrumbs";
// components
import Page from "../../components/general/Page";
import UploadAvatar from "../../components/general/upload/UploadAvatar";
import useFileUpload from "../../hooks/useFileUpload";
import useGeneralInfo from "../../hooks/user/useGeneralInfo";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// utils
import { fData } from "../../utils/formatNumber";

export default function GeneralInfo(): JSX.Element {
  const [profile, updateProfile] = useGeneralInfo();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: profile,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      console.log(JSON.stringify(values));
      const message = await updateProfile(values);
      setSubmitting(false);
      if (message === null) {
        resetForm();
        enqueueSnackbar("", { variant: "success" });
      } else {
        enqueueSnackbar(message, { variant: "error" });
      }
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  const uploadFile = useFileUpload();

  const handleDrop = useCallback(
    async acceptedFiles => {
      const file = acceptedFiles[0];
      const response = await uploadFile(file);
      if (typeof response === "string") {
        enqueueSnackbar(response, { variant: "error" });
      } else {
        const { url } = response[0];
        if (url === undefined) {
          enqueueSnackbar("", { variant: "error" });
        } else {
          await setFieldValue("avatar", url);
        }
      }
    },
    [uploadFile, enqueueSnackbar, setFieldValue]
  );

  return (
    <Page title="">
      <Container maxWidth={"lg"}>
        <HeaderBreadcrumbs
          heading=""
          links={[
            { name: "", href: PATH_DASHBOARD.profile },
            { name: "" },
          ]}
        />
        <FormikProvider value={formik}>
          <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
            <Grid container spacing={3}>
              <Grid item xs={2} md={1} />
              <Grid item xs={10} md={4}>
                <Card sx={{ py: 10, px: 3, textAlign: "center" }}>
                  <UploadAvatar
                    accept="image/*"
                    file={values.avatar}
                    maxSize={3145728}
                    onDrop={handleDrop}
                    error={Boolean(touched.avatar && errors.avatar)}
                    caption={
                      <Typography
                        variant="caption"
                        sx={{
                          mt: 2,
                          mx: "auto",
                          display: "block",
                          textAlign: "center",
                          color: "text.secondary",
                        }}
                      >
                         *.jpeg, *.jpg, *.png, *.gif 
                        <br />  {fData(3145728)}
                      </Typography>
                    }
                  />

                  <FormHelperText error sx={{ px: 2, textAlign: "center" }}>
                    {touched.avatar && errors.avatar}
                  </FormHelperText>
                </Card>
              </Grid>

              <Grid item xs={12} md={7}>
                <Card sx={{ p: 3 }}>
                  <Stack spacing={{ xs: 2, md: 3 }}>
                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        fullWidth
                        label=""
                        {...getFieldProps("wechat")}
                      />
                      <TextField
                        fullWidth
                        label=""
                        {...getFieldProps("weibo")}
                      />
                    </Stack>

                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        fullWidth
                        label=""
                        {...getFieldProps("location")}
                      />
                    </Stack>
                  </Stack>

                  <Box
                    sx={{ mt: 3, display: "flex", justifyContent: "flex-end" }}
                  >
                    <LoadingButton
                      type="submit"
                      variant="contained"
                      loading={isSubmitting}
                    >
                      
                    </LoadingButton>
                  </Box>
                </Card>
              </Grid>
            </Grid>
          </Form>
        </FormikProvider>
      </Container>
    </Page>
  );
}
